.cell {
    border: 1px solid black;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    color: darkturquoise;
}

.row {
    display: flex;
}

.gameOver {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 200px;
    color: white;
}

.num {
    text-align: center;
    color: brown;
}

#root {
    display: flex;
    justify-content: center;
}